body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.mainNav {
  background-color: transparent; }

.mainHeader {
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0.4) 100%), url(/static/media/bg-masthead.bde300d6.jpg); }

.mainHeader h1 {
  font-size: 65px;
  color: white;
  font-family: 'Open Sans', 'Helvetica Neue', Arial, sans-serif; }

.mainHeader .header-title {
  font-size: 18px;
  color: white; }

.mainHeader .header-button {
  font-family: 'Open Sans', 'Helvetica Neue', Arial, sans-serif;
  border-radius: 300px;
  font-weight: 900;
  text-transform: uppercase;
  margin-top: 10px;
  padding: 20px 20px 20px 20px;
  background-color: #f4623a;
  border: none; }

.container {
  margin-top: 115px;
  margin-bottom: 115px; }

.portrait {
  border-radius: 50%; }

.headerBody {
  padding-left: 50px; }

element.style {
  margin-bottom: 0px; }

.socials-container i {
  margin-right: 151px;
  margin-top: 50px;
  font-size: 60px;
  width: 42px;
  color: #222; }

.fa-linkedin:hover {
  transition: all 0.35s;
  opacity: 0.5; }

.fa-github-square:hover {
  transition: all 0.35s;
  opacity: 0.5; }

.fa-envelope-square:hover {
  transition: all 0.35s;
  opacity: 0.5; }

.description {
  background: linear-gradient(to bottom, #f4623a 0%, #f4623a 100%); }

.description h1 {
  color: white;
  text-align: center; }

.description hr {
  display: block;
  height: 1px;
  border: 0;
  border-radius: 5px;
  border-top: 3px solid white;
  margin: 1em 0;
  padding: 0; }

.description p {
  color: white; }

.project {
  padding: 4rem 2rem; }

.project-title {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  margin-left: 15px; }

.projects hr {
  display: block;
  height: 1px;
  border: 0;
  border-radius: 5px;
  border-top: 3px solid #f4623a;
  margin: 1em 0;
  padding: 0; }

.item hr {
  display: block;
  height: 1px;
  border: 0;
  border-radius: 5px;
  border-top: 3px solid #f4623a;
  margin: 1em 0;
  padding: 0; }

.under-title {
  border-color: #F05F40;
  border-width: 3px;
  max-width: 80px;
  margin-left: 210px; }

.project-image:hover {
  transition: all 0.35s;
  opacity: 0.5;
  color: #F05F40; }

.experience {
  color: white;
  background: linear-gradient(to bottom, #222 0%, #222 100%); }

.exp-header {
  text-align: center;
  padding: 4rem 2rem; }

.job-icon {
  font-size: 7em;
  padding-right: 20px; }

.job-container {
  padding-top: 20px;
  padding: 4rem 2rem; }

.education {
  background: linear-gradient(to bottom, #f4623a 0%, #f4623a 100%);
  color: white; }

.education hr {
  display: block;
  height: 1px;
  border: 0;
  border-radius: 5px;
  border-top: 3px solid white;
  margin: 1em 0;
  padding: 0; }

.education h1 {
  text-align: center; }

.edu-icon {
  font-size: 5em;
  padding-right: 20px; }

.lhl {
  margin-top: 50px;
  margin-bottom: 50px; }

.bcit {
  margin-top: 50px;
  margin-bottom: 50px; }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

